.model-tabs {
  &__tabs {
    background-color: #2c2c2c; } }

.model-tabs-list {
  $this: &;
  &__list {
    display: flex;
    @include md {
      display: block;
      font-size: 0; }
    @include xs {
      margin-left: -15px;
      margin-right: -15px; } }
  &__item {
    display: inline-block;
    flex-grow: 1;
    @include md {
      width: 50%;
      vertical-align: top; }
    @include xs {
      width: 100%; } }
  &__link {
    display: block;
    height: 100%;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
    @include bold;
    font-size: 15px;
    border-bottom: 2px solid transparent;
    transition: border-color .3s, background-color .3s;
    padding: 20px 10px;
    sup {
      vertical-align: super;
      font-size: 10px; }
    @include hover {
      @extend .model-tabs-list__link.is-active; }
    &.is-active {
      border-bottom-color: $c-accent;
      background-color: #353535;
      #{$this}__icon {
        @include icon-arrow(8, 8, 1, currentColor, 135);
        top: 1px; } } }
  &__icon {
    display: inline-block;
    @include icon-arrow(8, 8, 1, currentColor, -45);
    position: relative;
    margin-left: 8px;
    top: -3px;
    transition: transform .3s; } }

.complectation-list {
  &__list {
    @include md {
      margin-bottom: 20px; } }
  &__item {
    @include thin;
    font-size: 15px;
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 25px;
      @include md {
        margin-bottom: 20px; } } }
  &__icon {
    display: inline-block;
    position: relative;
    text-align: center;
    width: 17px;
    height: 17px;
    line-height: 17px;
    color: #adadad;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #2c2c2c;
    margin-right: 12px;
    @include icon-close(17, 2, #adadad);
    &::before,
    &::after {
      height: 8px;
      content: '';
      position: absolute;
 }      // +vcenter
    &::after {
      transform: rotate(90deg);
      top: 4px;
      left: 8px; }
    &::before {
      transform: rotate(0deg);
      top: 4px;
      left: 8px; } } }

.model-tab-item {
  padding-top: 35px;
  padding-bottom: 35px;
  background-color: #f7f7f7;
  display: none;
  &.is-active {
 }    // display: block
  &__title {
    margin-bottom: 30px; }
  &__img {
    position: relative;
    padding-top: 55%;
    iframe {
      @include coverdiv; }
    .spritespin-instance {
      position: absolute !important;
      width: 100% !important;
      height: 100% !important;
      top: 0;
      left: 0;
      cursor: grab; } } }

.tech-table {
  $this: &;
  width: calc(100% + 15px);
  @include sm {
    width: 100%; }
  &__tr {
    border-top: 1px solid  $c-border;
    border-bottom: 1px solid  $c-border;
    background-color: #efefef;
    &:nth-child(even) {
      background-color: transparent; } }
  &__th {
    font-weight: 400;
    text-align: left;
    @include bold;
    font-size: 15px;
    padding: 10px;
    padding-left: 35px;
    width: 40%;
    @include lg {
      width: 50%; } }
  &__td {
    @include thin;
    font-size: 15px;
    padding: 10px;
    width: 60%;
    @include lg {
      width: 50%; } }

  &--left {
    border-left: 1px solid  $c-border;
    margin-right: -15px;
    @include sm {
      margin-right: 0;
      border-right: 1px solid  $c-border; } }

  &--right {
    border-right: 1px solid  $c-border;
    margin-left: -15px;
    @include sm {
      margin-left: 0;
      border-left: 1px solid  $c-border;
      #{$this}__tr {
        background-color: #efefef;
        &:nth-child(odd) {
          background-color: transparent; } } } } }
